/** @jsx jsx */
import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Img from 'gatsby-image'
import { jsx, Styled, Container } from 'theme-ui';
import Layout from '../components/Layout';
import CallToAction from '../components/CallToAction';
import ContactForm from '../components/ContactForm';
import MediaLinkList from '../components/MediaLinkList';
import Section from '../components/Section';

function LandingPage(props) {
  const data = useStaticQuery(graphql`
    fragment fixedImage on File {
      childImageSharp {
        fixed(width: 280, height: 432) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    query {
      themeData {
        heading
        subheading
        linksHeading
        contactAddress
        minOfferAmount
        mainParagraph
        mediaLinks {
          id
          title
          url
        }
      }

      coinsImg: file(relativePath: { eq: "coins.png" }) {
        ...fixedImage
      }

      coinsImg2x: file(relativePath: { eq: "coins@2x.png" }) {
        ...fixedImage
      }

      coinsImg3x: file(relativePath: { eq: "coins@3x.png" }) {
        ...fixedImage
      }
    }
  `);
  const { themeData, coinsImg, coinsImg2x, coinsImg3x } = data
  const imgSources = [
    coinsImg.childImageSharp.fixed,
    {
      ...coinsImg2x.childImageSharp.fixed,
      media: `(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)`
    },
    {
      ...coinsImg3x.childImageSharp.fixed,
      media: `(-webkit-min-device-pixel-ratio: 3), (min-resolution: 288dpi)`
    },
  ]
  return (
    <Layout>
      <Container>
        <Section hideMobile>
          <div sx={{ textAlign: 'right', mb: 4 }}>
            <Img fixed={imgSources} alt="currency coins" objectFit="contain" />
          </div>
        </Section>
        <Section section='right' style={{ gridColumn: ['span 2', undefined] }}>
          <CallToAction heading={themeData.heading} subheading={themeData.subheading} contactAddress={themeData.contactAddress} />
        </Section>
      </Container>
      <div sx={{
        backgroundColor: "white",
        color: "gray.2"
      }}>
        <Container>
          <Section style={{ gridColumn: 'span 2' }}>
            <Styled.h1>Become an industry leader</Styled.h1>
            {themeData.mainParagraph.split('\n').map(line => <p dangerouslySetInnerHTML={{ __html: line }}></p>)}
          </Section>
        </Container>
      </div>
      <Container>
        <Section style={{ gridColumn: 'span 2' }}>
          <Styled.h1 sx={{ color: 'white' }}>{themeData.linksHeading}</Styled.h1>
          <MediaLinkList mediaLinks={themeData.mediaLinks} />
        </Section>
      </Container>
    </Layout>
  );
}

export default LandingPage