/** @jsx jsx */
import React, { useState } from 'react'
import { jsx, Styled } from 'theme-ui'

export default function ({ heading, subheading, contactAddress }) {
  const [showValue, setShowValue] = useState(false)
  return (
    <React.Fragment>
      <h1 sx={{ fontSize: 6, mt: 0, mb: 2, fontWeight: 800 }}>{heading}</h1>
      <h3 sx={{ fontWeight: 200, mt: 0, fontSize: 4 }}>{subheading.split(' ').map(part => {
        if (part.startsWith('#')) {
          return <React.Fragment><strong onClick={() => setShowValue(true)}
            sx={{
              bg: showValue ? `none` : `white`,
              color: showValue ? `white` : `#464EFF`,
              cursor: `pointer`,
              px: 2
            }}
          >{showValue ? contactAddress : 'CLICK TO REVEAL EMAIL'}</strong>&nbsp;</React.Fragment>
        }
        return `${part} `
      })}</h3>
    </React.Fragment>
  )
}