/** @jsx jsx */
import React from 'react'
import { jsx, } from 'theme-ui'

const sections = {
  left: [
    'grid-area',
    'left'
  ],
  right: [
    'grid-area', 
    'right'
  ]
}

// const variants = {
//   span2: [
//     'gridColumn',
//     'span 2'
//   ]
// }

function Section({ children, section = 'left', style, hideMobile = false }) {
  const [ gridArea, sectionValue ] = sections[section]
  const display = hideMobile ? ['none', 'block'] : 'block'
  const _sx = {
    [gridArea]: sectionValue,
    py: 5,
    display,
    ...style,
  }
  return (
    <section sx={_sx}>
    {children}
    </section>
  )
}

export default Section