import React from 'react'
import { Styled } from 'theme-ui'
import MediaLink from './MediaLink'

function MediaLinkList ({ mediaLinks }) {
  return (
    <Styled.ul>
      {mediaLinks.map(link => (
        <Styled.li key={link.id}>
          <MediaLink
            title={link.title} 
            url={link.url} 
          />
        </Styled.li>)
      )}
    </Styled.ul>
  )
}

export default MediaLinkList