import React from 'react'
import Helmet from 'react-helmet'
import { Layout, Main } from 'theme-ui'
import { Global, css } from '@emotion/core'

const Tag = () => {
  window.dataLayer = window.dataLayer || [];
  function gtag() { dataLayer.push(arguments); }
  gtag('js', new Date());

  gtag('config', 'UA-61782548-2');
  return null
}

export default ({ children }) => {
  return (
    <Layout>
      <Helmet>
        <title>Digital Wallet | digitalwallet.com</title>
      </Helmet>
      <Main>
        {children}
      </Main>
      <Global
        styles={css`
          body { margin: 0; color: "#FFF"; }
        `}
      />
      <script async src="https://www.googletagmanager.com/gtag/js?id=UA-61782548-2"></script>
      <script>
        {<Tag />}
      </script>
    </Layout>
  )
}